*,
*::before,
*::after {
  box-sizing: border-box;
}

:root {
  /* font-size: clamp(1rem, 1.05rem + 0.5vw, 1.125rem); */
  line-height: 1.5;
}
@import url('https://fonts.googleapis.com/css2?family=Playfair+Display:wght@400;700&display=swap');

h1,
h2,
h3,
h4,
h5,
figure,
p,
ol,
ul {
  margin: 0;
}

ol[role='list'],
ul[role='list'] {
  list-style: none;
  padding-inline: 0;
}

h1,
h2,
h3,
h4,
h5 {
  font-size: inherit;
  font-weight: inherit;
}
input,
textarea,
select,
button {
  padding: 0;
  margin: 0;
  font-family: inherit;
  font-size: 100%;
}

img {
  width: 8rem;
}
body {
  font-family: 'Playfair Display', system-ui, 'Segoe UI', Roboto, Oxygen, Ubuntu,
    Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  padding: 2rem;
  background-color: #1c1c1c;
  color: #c9a227;
}
header {
  display: grid;
  justify-content: center;
  grid-template-rows: 1fr 1fr;
  grid-template-areas:
    'logo'
    'nav';
}
.logo-container {
  display: flex;
  flex-direction: column;
  grid-area: logo;
  align-items: center;
  height: 9rem;
}
.logo-container > h1 {
  font-weight: bold;
  font-size: 2.5rem;
}
header > nav {
  grid-area: nav;
  display: flex;
  justify-content: space-around;
  width: 40rem;
  height: 8rem;
}
header button {
  border: none;
  font-size: 1.5rem;
  background: none;
  cursor: pointer;
  color: #f2f2f2;
}
button:hover {
  color: #9b7e1e;
}
.content-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: 2rem;
  font-weight: 550;
  gap: 1rem;
  margin-bottom: 1rem;
}
.content-container img {
  width: 75rem;
}
footer {
  display: flex;
  justify-content: space-around;
  font-size: 1rem;
  width: 30rem;
}
.active {
  color: #c9a227;
}
.menus,
.menu-item {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.menus {
  gap: 2rem;
}
.menu-item {
  /* border-bottom: 1px solid #c9a227; */
  line-height: 1;
}
.menu-item>img{
  width: 25rem;
  margin-bottom: 0.4rem;
}

.about{
  text-align: left;
  font-size: 1.7rem;
}
